html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", serif;
  font-size: 1.6rem;
  font-weight: 300;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

h1,
h2,
h3 {
  margin: 0;
}

p {
  margin: 0;
}

a {
  text-decoration: none;
  color: #000000;
  font-weight: 400;
}

.header {
  height: 7.2rem;
  display: grid;
  align-items: center;
  grid-template-columns: auto 1fr auto;
  grid-template-areas: "logo menu button";
  justify-items: center;
}
.header__title {
  grid-area: logo;
  font-size: 1.6rem;
  letter-spacing: 2;
}
.header__nav {
  grid-area: menu;
}
.header__nav ul {
  display: flex;
  gap: 4rem;
}
.header__nav ul a {
  text-transform: uppercase;
  font-size: 1.6rem;
  position: relative;
  color: #000;
  text-decoration: none;
}
.header__nav ul a::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: -2px;
  width: 100%;
  height: 2px;
  background: #000;
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 0.4s ease-in-out;
}
.header__nav ul a:hover::after {
  transform: scaleX(1);
}
.header__button {
  grid-area: button;
  color: #0284c7;
  border: 1px solid #0284c7;
  border-radius: 3rem;
  padding: 5px 1.7rem;
}
.header__button:hover {
  color: #ffffff;
  background-color: #0284c7;
}

.about {
  padding-block: 10rem;
  display: flex;
  justify-content: center;
  gap: 9.5rem;
}
.about__info {
  display: flex;
  flex-direction: column;
  gap: 1.6rem;
}
.about__detail {
  display: flex;
  flex-direction: column;
  gap: 0.7rem;
}
.about__saludate {
  font-size: 1.8rem;
  color: #0284c7;
  font-weight: bold;
}
.about__title {
  font-size: 4rem;
  color: #334155;
}
.about__text {
  max-width: 55rem;
  font-size: 1.6rem;
  font-weight: 400;
  color: #6b7280;
  line-height: 1.3;
}
.about__buttons a {
  display: inline-block;
  font-weight: 600;
  font-size: 1.4rem;
  padding: 1.2rem 2.6rem;
  border-radius: 3rem;
}
.about__buttons a:first-child {
  margin-right: 1.2rem;
  color: #ffffff;
  background-color: #0284c7;
  border: 1px solid transparent;
}
.about__buttons a:first-child:hover {
  color: #0284c7;
  background-color: transparent;
  border-color: #0284c7;
}
.about__buttons a:last-child {
  color: #0284c7;
  border: 1px solid #0284c7;
}
.about__buttons a:last-child:hover {
  color: #ffffff;
  background-color: #0284c7;
}
.about__social {
  display: flex;
  gap: 2.1rem;
  color: #6b7280;
  font-size: 3rem;
}
.about__social i {
  cursor: pointer;
}
.about__social i:hover {
  opacity: 0.6;
}
.about__image {
  display: block;
  width: 100%;
  max-width: 26.1rem;
  height: 26.1rem;
  object-fit: cover;
  border-radius: 50%;
  border: 1px solid rgba(63, 61, 86, 0.3);
}

.vector-1 {
  width: 100%;
  object-fit: cover;
}

.education {
  padding-top: 3.6rem;
  background-color: #0284c7;
  display: flex;
  justify-content: center;
  align-items: center;
}
.education__header {
  text-align: center;
  color: #ffffff;
  display: flex;
  flex-direction: column;
  gap: 2px;
  margin-bottom: 6.9rem;
}
.education__title {
  font-size: 4rem;
}
.education__separator {
  display: flex;
  align-items: center;
  justify-content: center;
}
.education__separator::before, .education__separator::after {
  content: "";
  display: block;
  width: 72px;
  height: 3px;
  background-color: #ffffff;
}
.education__separator::before {
  border-top-left-radius: 2px;
  border-bottom-left-radius: 2px;
}
.education__separator::after {
  border-top-right-radius: 2px;
  border-bottom-right-radius: 2px;
}
.education__separator > span {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: #0284c7;
  display: block;
  border: 2px solid #ffffff;
}
.education__subtitle {
  font-size: 2rem;
  font-weight: 500;
}
.education__timeline {
  display: flex;
  justify-content: center;
}
.education__timeline .timeline {
  position: relative;
  width: 947px;
  max-width: 947px;
}
.education__timeline .circle {
  width: 33px;
  height: 33px;
  background-color: #334155;
  border-radius: 50%;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}
.education__timeline .timeline-line {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 8px;
  height: 26.7rem;
  background-color: white;
  transform: translate(-50%, -50%);
  border-radius: 8px;
}
.education__timeline .timeline-item {
  display: flex;
  align-items: center;
  position: relative;
}
.education__timeline .timeline-item.left {
  justify-content: flex-start;
  margin-bottom: 4.3rem;
}
.education__timeline .timeline-item.right {
  justify-content: flex-end;
}
.education__timeline .content {
  background: white;
  padding: 2.5rem 1.7rem;
  border-radius: 2rem;
  box-shadow: 0px 4px 10px rgba(255, 255, 255, 0.25);
  width: 100%;
  max-width: 43.5rem;
  height: 12.8rem;
}
.education__timeline .content__title {
  color: #334155;
  font-size: 2.4rem;
}
.education__timeline .content__date {
  font-weight: 400;
  color: #6b7280;
  opacity: 0.5;
  margin-bottom: 1rem;
}
.education__timeline .content__p {
  font-weight: 400;
  color: #6b7280;
}

.skills {
  padding-top: 4.1rem;
  padding-bottom: 6rem;
  text-align: center;
}
.skills__header {
  text-align: center;
  color: #334155;
  display: flex;
  flex-direction: column;
  gap: 2px;
  margin-bottom: 8.5rem;
}
.skills__separator {
  display: flex;
  align-items: center;
  justify-content: center;
}
.skills__separator::before, .skills__separator::after {
  content: "";
  display: block;
  width: 5.7rem;
  height: 3px;
  background-color: #334155;
}
.skills__separator::before {
  border-top-left-radius: 2px;
  border-bottom-left-radius: 2px;
}
.skills__separator::after {
  border-top-right-radius: 2px;
  border-bottom-right-radius: 2px;
}
.skills__separator > span {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: #ffffff;
  display: block;
  border: 2px solid #334155;
  position: relative;
}
.skills__separator > span:not(:last-child) {
  margin-right: 18px;
}
.skills__separator > span:not(:last-child)::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 100%;
  transform: translateY(-50%);
  width: 20px;
  height: 3px;
  background-color: #334155;
}
.skills__title {
  font-size: 4rem;
}
.skills__subtitle {
  font-size: 2rem;
  font-weight: 500;
  max-width: 30rem;
  margin-inline: auto;
}
.skills__detail {
  display: grid;
  grid-template-columns: repeat(6, auto);
  grid-template-rows: auto;
  row-gap: 4.4rem;
  align-items: end;
}
.skills__info, .skills__level {
  font-weight: bold;
}
.skills__info {
  margin-top: 8px;
  color: #6b7280;
}
.skills__level {
  margin-top: 2px;
  font-size: 1.2rem;
  padding: 2px 2.1rem;
  width: fit-content;
  margin-inline: auto;
  border-radius: 3rem;
}
.skills .basic {
  background-color: #e0f2fe;
  color: #49a7d8;
}
.skills .medium {
  background-color: #fef3c7;
  color: #de8821;
}
.skills .advanced {
  background-color: #b5e5c4;
  color: #2d8659;
}
.skills__image {
  max-width: 6.5rem;
  margin-inline: auto;
}

.contact {
  padding-block: 8.4rem 13.1rem;
}
.contact__header {
  text-align: center;
  color: #334155;
  display: flex;
  flex-direction: column;
  gap: 2px;
  margin-bottom: 7.2rem;
}
.contact__title {
  font-size: 4rem;
}
.contact__separator {
  display: flex;
  align-items: center;
  justify-content: center;
}
.contact__separator::before, .contact__separator::after {
  content: "";
  display: block;
  width: 2.7rem;
  height: 3px;
  background-color: #334155;
}
.contact__separator::before {
  border-top-left-radius: 2px;
  border-bottom-left-radius: 2px;
}
.contact__separator::after {
  border-top-right-radius: 2px;
  border-bottom-right-radius: 2px;
}
.contact__separator > span {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: #ffffff;
  display: block;
  border: 2px solid #334155;
  position: relative;
}
.contact__separator > span:not(:last-child) {
  margin-right: 18px;
}
.contact__separator > span:not(:last-child)::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 100%;
  transform: translateY(-50%);
  width: 20px;
  height: 3px;
  background-color: #334155;
}
.contact__subtitle {
  font-size: 2rem;
  font-weight: 500;
}
.contact__detail {
  display: flex;
  gap: 12.3rem;
  justify-content: center;
}
.contact__form {
  box-shadow: 0px 4px 4px 1px rgba(0, 0, 0, 0.25);
  border-radius: 2rem;
  padding-block: 6.4rem 8.7rem;
  padding-inline: 4.5rem 2.7rem;
  width: 395px;
  max-width: 395px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.contact__form .form__group {
  display: flex;
  flex-direction: column;
  gap: 2px;
}
.contact__form .form__group label {
  font-weight: 500;
  color: #6b7280;
  font-size: 1.6rem;
}
.contact__form input,
.contact__form select {
  outline: none;
  padding: 4.5px;
  border: 1px solid #6b7280;
  border-radius: 5px;
  color: #6b7280;
}
.contact__form .form__button {
  text-align: center;
  margin-top: 2.5rem;
}
.contact__form .form__button button {
  background-color: #0284c7;
  color: #ffffff;
  border: 1px solid #0284c7;
  border-radius: 5px;
  font-weight: 600;
  font-size: 1.2rem;
  padding: 1.1rem 5rem;
  cursor: pointer;
}
.contact__form .form__button button:hover {
  background-color: #ffffff;
  color: #0284c7;
}

.footer {
  text-align: center;
  color: #ffffff;
}
.footer__top {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: end;
}
.footer__bottom {
  background-color: #0284c7;
  padding-top: 9px;
  height: 7.5rem;
}
.footer__text {
  font-weight: 300;
  font-size: 1.8rem;
}
.footer__detail {
  margin-bottom: 1.5rem;
  position: absolute;
}
.footer__detail h2 {
  font-size: 3rem;
  font-weight: 700;
}
.footer__detail p {
  margin-top: 5px;
  font-size: 1.8rem;
  font-weight: 600;
}
.footer__social {
  margin-top: 3.5rem;
  display: flex;
  justify-content: center;
  gap: 1.2rem;
  font-size: 4rem;
}
.footer__social i {
  color: #ffffff;
}
.footer__social a:hover {
  opacity: 0.8;
}